import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';


@Component({
  selector: 'app-registeradmin',
  templateUrl: './registeradmin.page.html',
  styleUrls: ['./registeradmin.page.scss'],
})
export class RegisteradminPage implements OnInit {

  usuarioadmin: string="";
  passadmin: string="";
  confirm_passadmin: string="";


  disabledButton;

  constructor(
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders

  ) { }

  ngOnInit() {
  }

  ionViewDiEnter(){
    this.disabledButton = false;
  }

  
  async tryRegisteradmin(){
    if(this.usuarioadmin==""){
      this.presentToast('Tu nombre es requerido');
    } else if(this.passadmin==""){
      this.presentToast('Se requiere la Contrase単a');
    } else if(this.confirm_passadmin!= this.passadmin){
      this.presentToast('Se requiere que confirme su Contrase単a');
    }else{
      this.disabledButton = true;
      const loader = await this.loadingCtrl.create({
        message: 'Espere por favor....',
      });
      await loader.present();

      return new Promise(resolve =>{
        let body = {
          aksi: 'proses_registeradmin',
          usuarioadmin: this.usuarioadmin,
          passadmin: this.passadmin
        }

        this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
          if(res.success==true){
            loader.dismiss();
            this.disabledButton = false;
            this.presentToast(res.msg);
            this.router.navigate(['/menu-admin']);
          }else{
            loader.dismiss();
            this.disabledButton = false;
            this.presentToast(res.msg);
          }
        },(err)=>{
          loader.dismiss();
          //this.presentToast('registro ');
          //this.router.navigate(['/login']);
          this.disabledButton = false;
          this.presentAlert('Timeout');

        });
        
      });
    }

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }

  async presentAlert(a) {
    const alert = await this.alertCtrl.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Close',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
            //action
          }
        }, {
          text: 'Try Again',
          handler: () => {
            this.tryRegisteradmin();
          }
        }
      ]
    });

    await alert.present();
  }


}
